section {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -10;
    overflow: hidden;
}

.set {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    /* filter: blur(1px); */
    
}

.set div {
    position: absolute;
    display: block;
}

.set div:nth-child(1) 
{
    left: 20%;
    -webkit-animation: animate 15s linear infinite;
            animation: animate 15s linear infinite;
    -webkit-animation-delay: -7s;
            animation-delay: -7s;
}
.set div:nth-child(2) 
{
    left: 50%;
    -webkit-animation: animate 20s linear infinite;
            animation: animate 20s linear infinite;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}
.set div:nth-child(3) 
{
    left: 70%;
    -webkit-animation: animate 25s linear infinite;
            animation: animate 25s linear infinite;
}
.set div:nth-child(4) 
{
    left: 0%;
    -webkit-animation: animate 15s linear infinite;
            animation: animate 15s linear infinite;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}
.set div:nth-child(5) 
{
    left: 85%;
    -webkit-animation: animate 18s linear infinite;
            animation: animate 18s linear infinite;
    -webkit-animation-delay: -10s;
            animation-delay: -10s;
}
.set div:nth-child(6) 
{
    left: 0%;
    -webkit-animation: animate 12s linear infinite;
            animation: animate 12s linear infinite;
}
.set div:nth-child(7) 
{
    left: 15%;
    -webkit-animation: animate 14s linear infinite;
            animation: animate 14s linear infinite;
}
.set div:nth-child(8) 
{
    left: 60%;
    -webkit-animation: animate 15s linear infinite;
            animation: animate 15s linear infinite;
}
@-webkit-keyframes animate 
{
    0%
    {
        opacity: 0;
        top: -10%;
        -webkit-transform: translateX(20px) rotate(0deg);
                transform: translateX(20px) rotate(0deg);
    }
    10%
    {
        opacity: 1;
        top: -10%;
    }
    20%
    {
        -webkit-transform: translateX(-20px) rotate(45deg);
                transform: translateX(-20px) rotate(45deg);
    }
    40%
    {
        -webkit-transform: translateX(-20px) rotate(90deg);
                transform: translateX(-20px) rotate(90deg);
    }
    60%
    {
        -webkit-transform: translateX(20px) rotate(135deg);
                transform: translateX(20px) rotate(135deg);
    }
    80%
    {
        -webkit-transform: translateX(-20px) rotate(180deg);
                transform: translateX(-20px) rotate(180deg);
    }
    100%
    {
        top: 110%;
        -webkit-transform: translateX(-20px) rotate(225deg);
                transform: translateX(-20px) rotate(225deg);
    }

}
@keyframes animate 
{
    0%
    {
        opacity: 0;
        top: -10%;
        -webkit-transform: translateX(20px) rotate(0deg);
                transform: translateX(20px) rotate(0deg);
    }
    10%
    {
        opacity: 1;
        top: -10%;
    }
    20%
    {
        -webkit-transform: translateX(-20px) rotate(45deg);
                transform: translateX(-20px) rotate(45deg);
    }
    40%
    {
        -webkit-transform: translateX(-20px) rotate(90deg);
                transform: translateX(-20px) rotate(90deg);
    }
    60%
    {
        -webkit-transform: translateX(20px) rotate(135deg);
                transform: translateX(20px) rotate(135deg);
    }
    80%
    {
        -webkit-transform: translateX(-20px) rotate(180deg);
                transform: translateX(-20px) rotate(180deg);
    }
    100%
    {
        top: 110%;
        -webkit-transform: translateX(-20px) rotate(225deg);
                transform: translateX(-20px) rotate(225deg);
    }

}

.set2 {
    -webkit-transform: scale(.5) rotateY(180deg);
            transform: scale(.5) rotateY(180deg);
    /* filter: blur(2px); */
}

.set3 {
    -webkit-transform: scale(.8) rotateX(180deg);
            transform: scale(.8) rotateX(180deg);
    /* filter: blur(4px); */
}


